﻿.valid.modified:not(.field, [type=checkbox]) {
    outline: 1px solid var(--tavenem-color-valid);
}

.invalid:not(.field) {
    outline: 1px solid var(--tavenem-color-error);
}

.validation-message {
    color: var(--tavenem-color-error);
}

#blazor-error-ui {
    background: var(--tavenem-color-yellow-bg);
    bottom: 0;
    box-shadow: 0 -1px 2px rgba(0, 0, 0, 0.2);
    display: none;
    left: 0;
    padding: 0.6rem 1.25rem 0.7rem 1.25rem;
    position: fixed;
    width: 100%;
    z-index: 9999;

    @media print {
        display: none;
    }

    .dismiss {
        cursor: pointer;
        position: absolute;
        right: 0.75rem;
        top: 0.5rem;
        text-decoration: none;
    }
}

.blazor-danger-boundary {
    background: url(data:image/svg+xml;base64,) no-repeat 1rem/1.8rem, var(--tavenem-color-danger-hover);
    padding: 1rem 1rem 1rem 3.7rem;
    color: var(--tavenem-color-danger-text);
}
.blazor-danger-boundary::after {
    content: "An error has occurred."
}
